import { Fragment, useState } from "react";
import { Dialog, Transition } from "@headlessui/react";
import { CodeBracketIcon, ShareIcon } from "@heroicons/react/24/outline";
import ShareOnSocial from "react-share-on-social";
import favicon from "./log.jpg";

export default function Result({ wpm, restart }) {
  const [open, setOpen] = useState(true);

  return (
    <Transition.Root show={open} as={Fragment}>
      <Dialog as="div" className="relative z-10" onClose={setOpen}>
        <Transition.Child
          as={Fragment}
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div
            className="fixed inset-0 bg-slate-600 bg-opacity-75 transition-opacity"
            onClick={() => restart()}
          />
        </Transition.Child>

        <div className="fixed inset-0 z-10 overflow-y-auto">
          <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
            <Transition.Child
              as={Fragment}
              enter="ease-out duration-300"
              enterFrom="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
              enterTo="opacity-100 translate-y-0 sm:scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 translate-y-0 sm:scale-100"
              leaveTo="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95"
            >
              <Dialog.Panel className="relative transform overflow-hidden rounded-lg bg-slate-500 px-4 pt-5 pb-4 text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-lg sm:p-6">
                <div>
                  <div className="mx-auto flex h-12 w-12 items-center justify-center rounded-full bg-green-100">
                    <CodeBracketIcon
                      className="h-8 w-8 text-slate-500"
                      aria-hidden="true"
                    />
                  </div>
                  <div className="mt-3 text-center sm:mt-5">
                    <Dialog.Title
                      as="h3"
                      className="text-lg font-light leading-6 text-slate-200"
                    >
                      Your typing speed is{" "}
                      <span className="text-red-50 font-mono text-lg">
                        {wpm} cpm(code per minute)
                      </span>
                    </Dialog.Title>
                  </div>
                </div>

                <div className="mt-5 sm:mt-6 sm:grid sm:grid-flow-row-dense sm:grid-cols-2 sm:gap-3">
                  <ShareOnSocial
                    textToShare="I got this score, try it now"
                    link="https://youtube.com"
                    linkTitle="Typing test from Abel and Henok"
                    linkMetaDesc="Share your score"
                    linkFavicon={favicon}
                    noReferer
                  >
                    <button
                      type="button"
                      className="inline-flex w-full items-center justify-center rounded-md border border-gray-300 bg-transparent px-4 py-2 text-base font-light text-white hover:text-slate-700 shadow-sm hover:bg-slate-300 sm:col-start-1"
                      onClick={() => {
                        setOpen(false);
                      }}
                    >
                      <ShareIcon className="mr-1 h-4 w-4" /> Share
                    </button>
                  </ShareOnSocial>
                  <button
                    type="button"
                    className="inline-flex w-full justify-center rounded-md border border-transparent bg-slate-600 px-4 py-2 text-base font-medium text-white shadow-sm hover:bg-slate-700 sm:col-start-2 sm:text-sm"
                    onClick={() => {
                      setOpen(false);
                      restart();
                    }}
                  >
                    Restart
                  </button>
                </div>
              </Dialog.Panel>
            </Transition.Child>
          </div>
        </div>
      </Dialog>
    </Transition.Root>
  );
}
